@import url(https://fonts.googleapis.com/css?family=Roboto);
.SideBar {
    background-color: #2b303b;
    /*width: 200px;*/
    color: white;
}

.hs-menu a {
    color: #788195;
}

.hs-menu nav {
    overflow-y: auto;
    position: relative;
    top: 10px;
    left: 0px;
    right: 0px;
    bottom: 40px;
    margin-top: 7px;
}

.hs-menu nav .active-sec .menu_name {
    color: #fff;
    transition: all .2s ease;
}

.hs-menu nav .active-sec .fa {
    color: #fff;
    transition: all .2s ease;
}

.hs-menu nav a {
    display: block;
    padding: 3px 5px;
    outline: none;
    width: 170px;
    text-decoration: none;
    transition: all .2s ease;
}

.hs-menu nav a:hover {
    color: #fff;
    transition: all .2s ease;
}

.hs-menu nav a:hover .fa {
    background: #788195;
    color: #2b303b;
    transition: all .2s ease;
}

.hs-menu nav a .menu_name {
    font-size: 13px;
    font-weight: 400;
    line-height: 40px;
    font-family: Montserrat;
}

.hs-menu nav a .fa {
    font-size: 21px;
    width: 40px;
    display: inline-block;
    height: 40px;
    line-height: 40px;
    text-align: center;
    background: #21252d;
    color: #788195;
    position: relative;
    left: 0;
    float: right;
}

.hs-menu .separator-aside {
    display: block;
    border-top: 1px solid #606777;
    height: 0;
    margin: 25px 0;
    margin-left: auto;
    margin-right: auto;
    width: 70px;
    opacity: 0.4;
}

.hs-menu .aside-footer {
    position: absolute;
    bottom: 0;
    height: 40px;
    width: 100%;
    line-height: 44px;
    text-align: center;
    background: #262A34;
}

.hs-menu .aside-footer a {
    width: 25px;
    height: 25px;
    line-height: 26px;
    padding: 0;
    text-align: center;
    font-size: 14px;
    margin: 0 2px;
    background: #2b303b;
    display: inline-block;
    transition: all .3s ease-in;
}

.hs-menu .aside-footer a:hover {
    color: #fff;
}

.fa {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-style: normal;
    font-feature-settings: normal;
    font-variant-ligatures: normal;
    font-variant-caps: normal;
    font-variant-numeric: normal;
    font-weight: normal;
    font-stretch: normal;
    font-size: inherit;
    line-height: 1;
    font-family: FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    transform: translate(0, 0);
}
header img {
  border-radius: 50%;
  margin: 0px auto;
  display: block;
  width: 165px;
  border: 5px solid #dedede; }

.profile-card {
  border-top: 0px solid #26A69A;
  border-bottom: 0px solid #00695C;
  border-radius: 50%;
  margin: 5% auto;
  display: block;
  height: 175px;
  width: 175px;
  background-size: cover;
  overflow: hidden;
  box-shadow: 0 0 100px 10px #eee;
  transition: all ease 0.3s; }

.profile-card:hover {
  margin: 0px auto;
  border-top: 4px solid #26A69A;
  border-bottom: 4px solid #00695C;
  border-radius: 5px;
  height: 100%;
  width: 190px;
  box-shadow: 0 0 70px 10px #eee; }

.profile-card:hover header img {
  margin: 20px auto;
  -webkit-animation: profile_image 2000ms linear both;
          animation: profile_image 2000ms linear both;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s; }

header {
  text-align: center; }

header h1 {
  position: relative;
  text-align: center;
  color: #fff;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
  font-size: 25px;
  line-height: 25px;
  display: inline-block;
  padding: 10px;
  transition: all ease 0.250s;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff; }

.profile-card:hover header h1 {
  margin-top: 0px;
  outline: 0 solid #000;
  border-top: 0px solid #fff;
  border-bottom: 1px solid #fff; }

header h2 {
  text-align: center;
  color: #fff;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
  font-size: 17px;
  font-weight: normal;
  line-height: 0px;
  margin: 0; }

.profile-bio {
  height: 70%;
  margin-top: 20px;
  padding: 1px 20px 10px 20px !important;
  transition: all linear 1.5s;
  color: #fff;
  font-size: 14px;
  opacity: 0;
  background: linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.42) 49%, rgba(0, 0, 0, 0.61) 100%); }

.profile-card:hover .profile-bio {
  opacity: 1;
  color: #000000; }

.profile-bio p:first-child {
  text-align: center;
  font-size: 16px; }

.profile-social-links {
  position: relative;
  margin-top: 0px;
  margin-left: -100px;
  list-style-type: none;
  opacity: 0;
  transition: all ease 0.5s;
  text-shadow: 2px 2px #000000; }

.profile-card:hover .profile-social-links {
  margin-left: -30px;
  opacity: 1;
  font-size: 40px;
  word-spacing: 10px;
  color: #01BDF2; }

.profile-social-links li {
  display: inline-block;
  color: #01BDFF;
  padding: 3px; }

.profile-social-links li img {
  width: 30px;
  background: #fff;
  border-radius: 50%;
  padding: 5px; }

